
@charset "UTF-8";
.query-tab-bottom-down.time-search {
  margin-top: 37px;
  background-color: #ffffff;
  width: 100%;
  border-bottom: 1px solid #dedede;
  border-top: 1px solid #dedede;
  /*transition*/
  /*下拉框*/
  /*弹出框*/
}
.query-tab-bottom-down.time-search .flex {
    border-radius: 2px;
    --margin: 10px;
    position: relative;
}
.query-tab-bottom-down.time-search .flex > input {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    border-radius: 2px;
    border: 1px solid #dedede;
    padding: 10px;
    font-size: 14px;
    background: url(data:image/svg+xml;base64,) no-repeat 8px 10px;
    padding-left: 36px;
    color: #666;
    position: relative;
}
.query-tab-bottom-down.time-search .flex > img {
    position: absolute;
    top: 10px;
    right: 10px;
}
.query-tab-bottom-down.time-search .fade-enter-active,
  .query-tab-bottom-down.time-search .fade-leave-active {
    -webkit-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
}
.query-tab-bottom-down.time-search .fade-enter,
  .query-tab-bottom-down.time-search .fade-leave-active {
    opacity: 0;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}
.query-tab-bottom-down.time-search .calendar-dropdown {
    background: #fff;
    position: absolute;
    left: 0;
    top: 0;
    padding: 10px 20px 10px 20px;
    border: 1px solid #eee;
    border-radius: 2px;
}
.query-tab-bottom-down.time-search .calendar-dropdown:before {
    position: absolute;
    left: 30px;
    top: -10px;
    content: "";
    border: 5px solid transparent;
    border-bottom-color: #DEDEDE;
}
.query-tab-bottom-down.time-search .calendar-dropdown:after {
    position: absolute;
    left: 30px;
    top: -9px;
    content: "";
    border: 5px solid transparent;
    border-bottom-color: #fff;
}
.query-tab-bottom-down.time-search .calendar-dialog {
    background: #fff;
    padding: 10px 20px 10px 20px;
    border: 1px solid #eee;
    border-radius: 2px;
}
.query-tab-bottom-down.time-search .bottom-button button {
    width: 50%;
    padding: 0px;
    border-radius: 0px;
}



