
.organization {
  font-size: 14px;
  margin-top: 40px;
  text-align: left;
  height: 100%;
  border-top: 1px solid #dedede;
}
.organization .content-input {
    --background-color: #dedede;
    height: 25px;
    line-height: 25px;
    padding: 5px 0px 5px 10px;
    font-size: 14px;
    color: #888;
    border: 1px solid #dedede;
    border-top: 0px;
}
.organization .content-input img {
      float: right;
      margin: 3px 20px;
}
.organization .tree-menu {
    overflow: auto;
    font-size: 16px;
    max-height: 350px;
    min-height: 300px;
    border-bottom: 1px solid #dedede;
}
.organization .organization-tree .active {
    color: red;
    font-weight: bold;
}
.organization .mint-searchbar {
    padding: 0px 0px 1px 0px;
}
.organization .bottom-button button {
    width: 50%;
    padding: 0px;
    border-radius: 0px;
}



