
.table2 {
  font-size: 14px;
  text-indent: 0;
  height: 100%;
  width: 100%;
  overflow: auto;
}
.table2 table {
    border-collapse: collapse;
}
.table2 table td {
      border: 1px solid #dedede;
      padding: 5px;
      text-align: center;
}
.table2 table thead > td {
      font-weight: bold;
}



