
.query-tab {
  text-align: center;
  padding: 0px;
  border-bottom: 1px solid #dedede;
  font-size: 14px;
}
.query-tab .query-tab-tab {
    width: 80%;
}
.query-tab .mint-popup-top {
    top: auto;
}
.query-tab .top-fixed {
    position: fixed;
    width: 100%;
    background-color: #fff;
    z-index: 1;
}
.query-tab .query-tab-query {
    width: 15%;
    text-align: right;
}
.query-tab .query-tab-query img {
      width: 20px;
      height: 20px;
      vertical-align: middle;
}
.query-tab .mint-tab-item-label {
    font-size: 15px;
}
.query-tab .mint-navbar .mint-tab-item {
    padding: 10px 0px;
}
.query-tab .mint-navbar .mint-tab-item.is-selected {
      border-bottom: 2px solid #EF4F4F;
      color: #EF4F4F;
}
.query-tab .query-tab-bottom {
    border-top: 1px solid #dedede;
    margin-top: 2px;
    background-color: #F6F6F6;
}
.query-tab .query-tab-bottom div {
      height: 38px;
      line-height: 38px;
      padding: 0px;
      border-bottom: 1px solid #dedede;
}
.query-tab .query-tab-bottom .search-active {
      border-bottom: 0px;
      background-color: #ffffff;
}
.query-tab .query-tab-bottom .query-tab-bottom-org {
      width: 35%;
}
.query-tab .query-tab-bottom .query-tab-bottom-status {
      width: 30%;
      margin-left: -1px;
      margin-right: -1px;
      border-left: 1px solid #dedede;
}
.query-tab .query-tab-bottom .query-tab-bottom-time {
      width: 35%;
      border-left: 1px solid #dedede;
}
.query-tab .cg-query .query-tab-bottom-org {
    border-left: 0px;
    border-right: 0px;
}
.query-tab .cg-query .query-tab-bottom-status {
    border-left: 0px;
    border-right: 0px;
    margin: 0px;
}
.query-tab .cg-query .query-tab-bottom-time {
    border-left: 0px;
    border-right: 0px;
}
.query-tab .heightauto {
    width: 101%;
    height: auto;
}
.query-tab .has-query {
    padding: 5px;
}



