
.my-popup .mint-popup-1 {
  width: 96% !important;
  height: 250px;
}
.my-popup .catalog {
  margin-top: 15%;
}
.my-popup .cata-content {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  text-indent: 0.5em;
  font-size: 20px;
  margin-bottom: 2%;
  margin-top: 5%;
}
.my-popup .mint-popup-1 {
  margin-bottom: 15%;
  font-size: 30px;
  text-indent: 0.6em;
}
.my-popup .advice {
  width: 86%;
}
.my-popup .pass {
  position: absolute;
  bottom: 10%;
  left: 20%;
}
.my-popup .back {
  position: absolute;
  bottom: 10%;
  right: 20%;
}
.my-popup div.cell {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.my-popup th div.cell {
  padding: 0px;
  height: 24px;
  width: 78px;
  margin-left: -15%;
}
.my-popup td div.cell {
  text-indent: 0em;
}
.my-popup .set-title {
  height: 25px;
  line-height: 25px;
  font-size: 14px;
  padding: 5px 5px 5px 20px;
  background-color: #F6F6F6;
}
.my-popup .set-container {
  border: 1px solid #dedede;
}
.my-popup .set-container .last {
    border-bottom: 0px !important;
}
.my-popup .set-container .set-body input {
    background-color: white;
}
.my-popup .set-container .set-body div {
    font-size: 15px;
    height: 30px;
    line-height: 30px;
    padding: 5px;
    border-bottom: 1px solid #dedede;
    margin-left: 30px;
    overflow: hidden;
    white-space: nowrap;
    word-break: break-all;
    text-overflow: ellipsis;
}
.my-popup .set-container .set-body div:empty:before {
      content: attr(placeholder);
      color: #888;
}
.my-popup .set-container .set-body div input {
      width: 100%;
      height: 100%;
      border: 0px;
      outline: none;
      font-size: 15px;
}
.my-popup .set-container .set-body .img-left {
    float: left;
    width: 20px;
    height: 20px;
    margin-top: 10px;
    margin-left: 5px;
}
.my-popup .set-container .set-body .img-right {
    float: right;
    width: 20px;
    height: 20px;
    margin-top: 10px;
    margin-right: 5px;
}



