
.product-detail-text {
  font-size: 16px;
  height: 100%;
}
.product-detail-text .tab-detail img {
    width: 90%;
    height: 100%;
}
.product-detail-text .title_content {
    margin-top: 15%;
    padding-bottom: 20px;
    padding-left: 1.5em;
    padding-right: 1.5em;
    --width: 100vw;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.product-detail-text .title {
    width: 90vw;
    --font-size: 22px;
    word-wrap: break-word;
    word-break: normal;
    display: inline-block;
    line-height: 35px;
}
.product-detail-text .title-onrow {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: break-all;
}
.product-detail-text .author_content {
    text-indent: 2em;
    font-size: 10px !important;
    margin: 10px 0px 10px 0px;
}
.product-detail-text .author {
    font-size: 9px !important;
    text-indent: 1em;
}
.product-detail-text .content {
    margin-left: 1.5em;
    margin-right: 1.5em;
    margin-bottom: 55px;
    word-break: break-all;
}



