
.product-container .line-body {
  padding-top: 76px;
}
.product-container .line-row {
  border-bottom: 1px solid #dedede;
  line-height: 22px;
}
.product-container .load-tip {
  text-align: center;
  line-height: 30px;
  margin-top: 10px;
  color: #000;
  font-size: 14px;
}
.product-container .load-tip img {
    width: 20px;
    height: 20px;
    vertical-align: middle;
}
.product-container .query-popup {
  width: 100%;
  height: 80%;
}
.product-container .create-button {
  position: fixed;
  font-size: 16px;
  left: 10px;
  bottom: 10px;
}



