
.issuer-operator .query-popup {
  width: 100%;
  height: 100%;
}
.issuer-operator .set-button {
  text-align: center;
}
.issuer-operator .set-button .mint-button {
    display: inline-block;
    width: 80%;
    height: 35px;
}
.issuer-operator .set-title {
  height: 25px;
  line-height: 25px;
  font-size: 14px;
  padding: 5px 5px 5px 20px;
  background-color: #F6F6F6;
}
.issuer-operator .set-container {
  border-bottom: 0px;
}
.issuer-operator .set-container .last {
    border-bottom: 0px !important;
}
.issuer-operator .set-container .set-body {
    border-bottom: 1px solid #dedede;
    font-size: 16px;
    margin-top: 60px;
    padding: 15px;
}
.issuer-operator .set-container .set-body .title {
      margin-top: 3px;
}



