
.header {
  font-size: 16px;
}
.header .more-opera {
    line-height: 40px;
    height: 40px;
    padding: 5px 5px 5px 25px;
    border-top: 1px solid #dedede;
}
.header .query-popup {
    width: 100%;
    height: auto;
}
.header div.mint-header-title {
    height: 100%;
    line-height: 40px;
}
.header .more-buttons button {
    color: #fff;
    background-color: #ef4f4f;
    display: inline-block;
    font-size: 14px;
    padding: 0 12px;
    height: 30px;
    margin: 0px 1px;
}



