
.search-content.common {
  height: 100%;
  overflow: auto;
}
.search-content.common .mint-search {
    position: absolute;
    width: 100%;
    height: auto;
}
.search-content.common .search-history {
    height: 100%;
    margin-top: 52px;
    text-align: left;
}
.search-content.common .search-mid-content {
    padding: 10px 10px 10px 20px;
    border-bottom: 1px solid #dedede;
}
.search-content.common .search-mid-content div {
      height: 30px;
      line-height: 30px;
}
.search-content.common .search-mid-content img {
      display: inline-block;
      vertical-align: middle;
}
.search-content.common .search-mid-content-txt {
    width: 90%;
}
.search-content.common .search-mid-content-img {
    width: 10%;
}
.search-content.common .search-bottom-btn {
    position: absolute;
    right: 10px;
    bottom: 10px;
}



