
.popup-add {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  position: relative;
  display: inline-block;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.popup-add .popupimg {
    border-radius: 500px;
    background-color: #ef4f4f;
}
.popup-add .popupimg img {
      width: 54px;
      height: 50px;
}
.popup-add .popuptext {
    visibility: hidden;
    width: 150px;
    border: 1px solid #ef4f4f;
    background-color: #fff;
    color: #000;
    text-align: left;
    border-radius: 5px;
    position: absolute;
    z-index: 1;
    bottom: 55px;
    left: 50%;
    --margin-left: -80px;
}
.popup-add .popuptext div {
      height: 30px;
      line-height: 30px;
      padding: 10px;
}
.popup-add .popuptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 5%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #ef4f4f transparent transparent transparent;
}
.popup-add .show {
    visibility: visible;
    -webkit-animation: fadeIn 1s;
    animation: fadeIn 1s;
}
@-webkit-keyframes fadeIn {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes fadeIn {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}



