
.line-cell .browsed {
  color: #888888 !important;
}
.line-cell .line-cell-top {
  padding: 5px 0px 5px 10px;
  position: relative;
}
.line-cell .line-cell-top div.line-cell-title {
    font-size: 15px;
    color: #000;
    min-height: 40px;
    margin-left: 40px;
    word-break: break-all;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    padding-top: 5px;
    margin-right: 40px;
}
.line-cell .line-cell-top .line-cell-icon {
    width: 30px;
    height: 30px;
    margin-top: 3px;
}
.line-cell .line-cell-top .status-icon {
    position: absolute;
    width: 12px;
    height: 12px;
    left: 28px;
    top: 25px;
}
.line-cell .line-cell-top .line-cell-opera {
    width: 40px;
    text-align: right;
    margin-top: 15px;
}
.line-cell .line-cell-top .line-cell-opera .row-menu {
      width: 32px;
      height: 32px;
      border-radius: 5px;
}
.line-cell .line-cell-mid {
  font-size: 14px;
  margin: 5px 0px 5px 50px;
}
.line-cell .line-cell-mid div {
    color: #888888;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.line-cell .line-cell-mid .element-time {
    width: 30%;
    text-align: left;
    vertical-align: middle;
}
.line-cell .line-cell-mid .element-name {
    width: 20%;
    text-align: center;
    vertical-align: middle;
}
.line-cell .line-cell-mid .element-dept {
    width: 45%;
    text-align: center;
    vertical-align: middle;
}



