
.new-img .container {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  clear: both;
}
.new-img .z_photo {
  width: 6.4rem;
  /*height: 8.8rem;*/
  padding-left: 0.1rem;
  overflow: auto;
  clear: both;
  margin: 0 auto;
  /*border: 1px solid #555;*/
}
.new-img .z_photo .z_addImg img {
  width: 1rem;
  height: 1rem;
  border: 2px solid white;
}
.new-img .imgborder {
  border: 2px solid firebrick !important;
}
.new-img .z_addImg {
  float: left;
  margin-right: 0.2rem;
  position: relative;
}
.new-img .description {
  width: 96%;
  clear: both;
}
.new-img .z_file {
  width: 1rem;
  height: 1rem;
  background: url(../../assets/image/z_add.png) no-repeat;
  background-size: 100% 100%;
  float: left;
  margin-right: 0.2rem;
  border: 2px solid white;
}
.new-img .z_file input::-webkit-file-upload-button {
  width: 1rem;
  /*    height: 1rem;*/
  border: none;
  position: absolute;
  outline: 0;
  opacity: 0;
}
.new-img .z_file input#file {
  display: block;
  width: 100%;
  height: 100%;
  border: 0;
  vertical-align: middle;
  outline: none;
}



