
.toastindex {
  z-index: 9999;
}
.cgaudit-operator .organization .tree-menu, .cgaudit-operator .audit-author .content-body {
  max-height: 90%;
}
.cgaudit-operator .query-popup {
  width: 100%;
}
.cgaudit-operator .set-button {
  margin-top: 20px;
  text-align: center;
}
.cgaudit-operator .set-button .mint-button {
    display: inline-block;
    width: 80%;
    height: 35px;
}
.cgaudit-operator .set-title {
  height: 25px;
  line-height: 25px;
  font-size: 14px;
  padding: 5px 5px 5px 20px;
  background-color: #F6F6F6;
}
.cgaudit-operator .set-container {
  border: 1px solid #dedede;
}
.cgaudit-operator .set-container .last {
    border-bottom: 0px !important;
}
.cgaudit-operator .set-container .set-body div {
    font-size: 15px;
    height: 30px;
    line-height: 30px;
    padding: 5px;
    border-bottom: 1px solid #dedede;
    margin-left: 30px;
    overflow: hidden;
    white-space: nowrap;
    word-break: break-all;
    text-overflow: ellipsis;
}
.cgaudit-operator .set-container .set-body div:empty:before {
      content: attr(placeholder);
      color: #888;
}
.cgaudit-operator .set-container .set-body div input {
      width: 100%;
      height: 100%;
      border: 0px;
      outline: none;
      font-size: 15px;
}
.cgaudit-operator .set-container .set-body .img-left {
    float: left;
    width: 20px;
    height: 20px;
    margin-top: 10px;
    margin-left: 5px;
}
.cgaudit-operator .set-container .set-body .img-right {
    float: right;
    width: 20px;
    height: 20px;
    margin-top: 10px;
    margin-right: 5px;
}



