
.editAttr .set-title {
  height: 25px;
  line-height: 25px;
  font-size: 14px;
  padding: 5px 5px 5px 20px;
  background-color: #F6F6F6;
}
.editAttr .set-container .last {
  border-bottom: 0px !important;
}
.editAttr .set-container .set-body div {
  font-size: 15px;
  height: 30px;
  line-height: 30px;
  padding: 5px;
  border-bottom: 1px solid #dedede;
  margin-left: 30px;
  overflow: hidden;
  white-space: nowrap;
  word-break: break-all;
  text-overflow: ellipsis;
}
.editAttr .set-container .set-body div:empty:before {
    content: attr(placeholder);
    color: #888;
}
.editAttr .set-container .set-body div input {
    width: 100%;
    height: 100%;
    border: 0px;
    outline: none;
    font-size: 15px;
}
.editAttr .set-container .set-body .img-left {
  float: left;
  width: 80px;
  height: 20px;
  margin-top: 10px;
  margin-left: auto;
  text-align: center;
  font-size: medium;
}
.editAttr .footBtn {
  margin: 2% auto;
  display: block;
}
.editAttr .bottom-button {
  width: 100%;
  background-color: #fff;
  margin: 0 auto;
  text-align: center;
  position: fixed;
  bottom: 2%;
}



