
.audit-author {
  font-size: 15px;
  margin-top: 40px;
  text-align: left;
  height: 100%;
  border-top: 1px solid #dedede;
}
.audit-author red {
    color: #ef4f4f;
    font-weight: bold;
}
.audit-author .icon.selected {
    float: right;
    margin: 5px 10px 0px 0px;
    background-image: url(../../assets/image/selected.png);
}
.audit-author .active {
    color: red;
}
.audit-author .mint-searchbar {
    padding: 0px 0px 1px 0px;
}
.audit-author .content {
    height: 100%;
    overflow: auto;
}
.audit-author .content-input {
    position: absolute;
    background-color: #fff;
    height: 25px;
    line-height: 25px;
    padding: 5px 0px 5px 10px;
    font-size: 14px;
    color: #888;
    width: 100%;
    border: 1px solid #dedede;
    border-top: 0px;
}
.audit-author .content-input img {
      float: right;
      margin: 3px 22px;
}
.audit-author .content-body {
    margin-top: 35px;
    border: 1px solid #dedede;
    border-top: 0px;
    max-height: 350px;
    min-height: 300px;
}
.audit-author .content-body div {
      overflow: hidden;
      white-space: nowrap;
      word-break: break-all;
      text-overflow: ellipsis;
      height: 25px;
      padding: 5px 0px 5px 10px;
      line-height: 30px;
      border-bottom: 1px solid #dedede;
}



