
.video-detail {
  font-size: 16px;
  height: 100%;
}
.video-detail img {
    width: 90%;
    height: 100%;
}
.video-detail .title_content {
    margin-top: 15%;
    padding-bottom: 20px;
    padding-left: 1.5em;
    padding-right: 1.5em;
    --width: 100vw;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.video-detail .title {
    width: 90vw;
    --font-size: 22px;
    word-wrap: break-word;
    word-break: break-all;
    display: inline-block;
    line-height: 35px;
}
.video-detail .footer {
    position: fixed;
    width: 99%;
    height: 10%;
    bottom: 0px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}



