
.product-detail-img {
  margin-top: 0px !important;
  font-size: 16px;
  height: 100%;
}
.product-detail-img .head {
    --margin-bottom: 20% !important;
}
.product-detail-img .swiper-box {
    width: 100%;
    height: 50%;
    margin: 0 auto;
}
.product-detail-img .swiper-item {
    text-align: center;
    font-size: 18px;
    background: #fff;
    /* Center slide text vertically */
    /*display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;*/
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.product-detail-img .swiper-slide img {
    display: block;
    margin: auto;
    height: 300px;
    width: 96%;
}
.product-detail-img .swiper-slide figure {
    margin: 0px;
}
.product-detail-img .full {
    width: 100%;
}
.product-detail-img .imgbox {
    height: auto;
    width: auto;
}
.product-detail-img figcaption {
    display: none;
}
.product-detail-img .pswp__caption__center {
    font-size: 20px;
    margin-bottom: 5%;
    word-wrap: break-word;
    word-break: normal;
    /* display: inline-block;*/
}
.product-detail-img .title_content {
    padding: 20px;
}
.product-detail-img .title {
    width: 90vw;
    --font-size: 22px;
    word-wrap: break-word;
    word-break: break-all;
    display: inline-block;
    line-height: 35px;
}
.product-detail-img .title-tip {
    text-align: right;
    font-size: 12px;
}
.product-detail-img .footer {
    position: fixed;
    width: 99%;
    height: 10%;
    bottom: 0px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}



