
.search-content {
  font-size: 16px;
}
.search-content .search-content-top {
    position: fixed;
    width: 100%;
}
.search-content .search-result {
    padding-top: 80px;
}
.search-content .search-result red {
      color: #ef4f4f;
      font-weight: bold;
}
.search-content .search-result em {
      color: #ef4f4f;
      font-weight: bold;
      font-style: normal;
}
.search-content .mint-search {
    width: 100%;
    height: auto;
}
.search-content .search-history {
    text-align: left;
    border-bottom: 1px solid #dedede;
    background-color: #fff;
}
.search-content .search-history div {
      line-height: 30px;
      padding: 2px 5px 2px 20px;
      font-size: 14px;
      color: #888;
}
.search-content .search-mid-content {
    font-size: 15px;
    padding: 5px 10px 5px 20px;
    border-bottom: 1px solid #dedede;
}
.search-content .search-mid-content div {
      height: 30px;
      line-height: 30px;
      overflow: hidden;
      white-space: nowrap;
      word-break: break-all;
      text-overflow: ellipsis;
}
.search-content .mint-searchbar {
    padding: 1px 0px;
    background-color: #dedede;
}
.search-content .mint-searchbar .mint-searchbar-core {
      --background-color: #ededed;
      --padding: 5px;
}
.search-content .mint-searchbar .mint-searchbar-inner {
      height: 35px;
}
.search-content .mint-searchbar a.mint-searchbar-cancel {
      margin: 0px 10px;
}
.search-content .load-tip {
    text-align: center;
    line-height: 30px;
    margin-top: 0px;
    color: #000;
    font-size: 14px;
}
.search-content .load-tip img {
      width: 20px;
      vertical-align: middle;
}
.search-content .bottom-tip {
    color: #888 !important;
}
.search-content .bottom-tip img {
      width: 20px;
      vertical-align: middle;
}



