
.audit .advice {
  -webkit-box-sizing: border-box !important;
          box-sizing: border-box !important;
  width: 100vw;
}
.audit .foot {
  margin: 0 auto;
  text-align: center;
}
.audit .query-popup {
  width: 100%;
  height: 100%;
}
.audit .turn {
  width: 100px;
}
.audit .set-body div {
  font-size: 15px;
  height: 30px;
  line-height: 30px;
  padding: 5px;
  border-bottom: 1px solid #dedede;
  margin-left: 30px;
  overflow: hidden;
  white-space: nowrap;
  word-break: break-all;
  text-overflow: ellipsis;
}
.audit .set-body div:empty:before {
    content: attr(placeholder);
    color: #888;
}
.audit .set-body div input {
    width: 100%;
    height: 100%;
    border: 0px;
    outline: none;
    font-size: 15px;
}
.audit .set-body .img-left {
  float: left;
  width: 20px;
  height: 20px;
  margin-top: 10px;
  margin-left: 5px;
}
.audit .set-body .img-right {
  float: right;
  width: 20px;
  height: 20px;
  margin-top: 10px;
  margin-right: 5px;
}



